
@import '../Variables/variables';
.scroll{
    position: fixed;
    bottom: 10px;
    width: 100%;
    z-index: 10000;
   .content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    button{
        border: none;
        background: transparent;
        border: none;
        background: $secondary-color;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        font-size: 25px;
        i{
            color: $primary-color;
        }
    }
   }
}